import Slider from "react-slick"
import './Slider.css'

function SliderImg() {
    const sliderUrls = [
        "/images/slider-1.webp",
        "/images/slider-2.webp",
        "/images/slider-3.webp",
        "/images/slider-4.webp",
        "/images/slider-3.webp",
        "/images/slider-2.webp",
    ]

    const settings = {
        infinite: true,
        speed: 500,
        slidesToShow: 4,
        slidesToScroll: 1,
        adaptiveHeight: true,
        autoplay: true,
        autoplaySpeed: 2000,
        arrows: false,
        responsive: [
            {
              breakpoint: 1024,
              settings: {
                infinite: true,
                speed: 500,
                slidesToShow: 3,
                slidesToScroll: 1,
                adaptiveHeight: true,
                autoplay: true,
                autoplaySpeed: 2000,
                arrows: false,
              }
            },
            {
                breakpoint: 800,
                settings: {
                    infinite: true,
                    speed: 500,
                    slidesToShow: 2,
                    slidesToScroll: 1,
                    adaptiveHeight: true,
                    autoplay: true,
                    autoplaySpeed: 2000,
                    arrows: false,
                }
            },
        ]
    }

    return (
        <div className="slider-area">
            <div className="container">
                <Slider {...settings}>
                    {sliderUrls.map((item, i) => (
                        <div className="slider-image">
                            <img src={item} key={i}/>
                        </div>
                    ))}
                </Slider>
            </div>
        </div>
    )
}

export default Slider